export const createPage8MenuComp = ({

}) => {
  const article = document.createElement('article');

  const section = `
  <script type="module" src="/U8/WebComponentMenuComponent.js"></script>
  <div>
  <simple-menucomponent id="Menu1"></simple-menucomponent>
  <simple-menucomponent id="Menu2"></simple-menucomponent>
	</div>
  <script> 
    menu1 = document.getElementById("Menu1")
    console.log(menu1);
    menu1.items = [];
    menu1.items.push(
      {
        link: "https://google.de",
        text: "Google"
      }
    )
    menu1.items.push(
      {
        link: "https://www.w3schools.com/",
        text: "W3Schools"
      }
    )
    menu1.items.push(
      {
        link: "https://wiki.selfhtml.org/",
        text: "SelfHTML"
      }
    )

    menu2 = document.getElementById("Menu2")
    
    menu2.items = [];
    menu2.items.push(
      {
        link: "https://google.de",
        text: "Google"
      }
    )
    menu2.items.push(
      {
        link: "https://www.w3schools.com/",
        text: "W3Schools"
      }
    )
    menu2.items.push(
      {
        link: "https://wiki.selfhtml.org/",
        text: "SelfHTML"
      }
    )
    menu2.columnMode = true;

  </script>

`;
  document.getElementById("Menu")
  article.insertAdjacentHTML('beforeend', section);

  return section;
};
